* {
  margin: 0;
  padding: 0;
  font-family: Times New Roman, Times, serif;
  font-size: 20px;
}

html {
  width: 100%;
}

header {
  height: 10vw;
  text-align: center;
  background-color: #000;
}

header div {
  height: 5vw;
  display: inline-block;
  position: absolute;
  top: 2.5vw;
  left: 2.5vw;
}

header div object, footer div object {
  height: 100%;
}

nav {
  width: 50%;
  height: 5vw;
  background-color: #fff;
  display: inline-block;
  position: relative;
  top: 25%;
}

nav ul {
  height: 100%;
  width: 100%;
  display: inline-block;
  position: relative;
}

nav li {
  height: 50%;
  padding-left: 20px;
  padding-right: 20px;
  display: inline-block;
  position: relative;
  top: 25%;
}

nav li a {
  height: 50%;
  color: #000;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  list-style: none;
  display: inline-block;
  position: relative;
  top: 25%;
}

nav li a:hover {
  color: gold;
}

nav li a[class="activo"] {
  border-color: gold;
  border-bottom-style: dashed;
  border-bottom-width: 3px;
  padding-bottom: 5px;
}

footer {
  height: 10vw;
  text-align: center;
  text-align: left;
  background-color: #000;
  padding-left: 5vw;
}

footer div {
  width: 80%;
  height: 5vw;
  display: inline-block;
  position: relative;
  top: 20%;
}

footer div ul {
  height: 100%;
  width: 100%;
  display: inline-block;
  position: relative;
}

footer div ul li {
  height: 50%;
  padding-left: 20px;
  padding-right: 20px;
  display: inline-block;
  position: relative;
  top: 25%;
}

footer ul li a {
  height: 50%;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  list-style: none;
  display: inline-block;
  position: relative;
  top: 25%;
}

footer ul li a:hover {
  color: gold;
}

main div {
  text-align: center;
  margin: 20px 10vw;
}

main div img, main div object {
  width: 25vw;
  display: inline-block;
  position: relative;
}

div [id="logo2"] {
  width: 80%;
}

ul li img {
  width: 4vw;
}

figcaption {
  font-size: 12px;
}

h1, h2 {
  text-align: left;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 30px;
}

p {
  text-align: left;
  margin-top: 10px;
  margin-bottom: 10px;
}

main div ul {
  text-align: left;
  text-transform: uppercase;
  background-color: gold;
  padding: 20px;
  font-weight: bold;
}

h2 + ul {
  text-align: left;
  background-color: #0000;
  padding-left: 20px;
}

div ul li {
  padding: 10px;
  list-style-type: none;
}

h2 + ul li {
  text-transform: none;
  font-weight: normal;
  list-style-type: square;
}

div ul a {
  color: #000;
  text-decoration: none;
}

div ul a:hover {
  color: #fff;
}

div h2 + ul a:hover {
  color: gold;
}

li ul {
  margin-left: 20px;
}

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  border: 2px solid #000;
}

th {
  height: 50px;
  text-align: center;
  background-color: gold;
  border: 2px solid #000;
}

td {
  height: 30px;
  border: 2px solid #000;
  padding-left: 10px;
}

#slider {
  max-height: 700px;
  max-width: 700px;
  margin: auto;
}

#slider img, #slider object {
  size: auto;
}

@keyframes animation1 {
  0% {
    transform: scale(.5);
  }

  25% {
    transform: scale(1.5);
  }

  50% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

#animacion1 {
  animation-name: animation1;
  animation-duration: 4s;
  animation-iteration-count: 1;
}

#animacion1:hover {
  transform: scale(1.2);
}

@media (max-width: 1000px) {
  header {
    height: 25vw;
  }

  nav, header div object {
    height: 15vw;
  }

  header div {
    top: 5vw;
  }

  nav ul {
    display: block;
  }

  nav li {
    height: 2vw;
    display: block;
  }

  footer {
    height: 20vw;
  }

  footer div {
    top: 5%;
  }

  footer div ul {
    display: block;
  }

  footer div ul li {
    height: 1vw;
    display: block;
  }

  main div {
    width: 80vw;
  }

  #slider {
    max-height: 500px;
    max-width: 500px;
  }
}

@media (max-width: 600px) {
  * {
    font-size: 15px;
  }

  footer div {
    top: 1%;
  }

  footer div ul li {
    height: .2vw;
  }

  #slider {
    max-height: 300px;
    max-width: 300px;
  }
}

